.error-messages {
  color: red
}
ul.NavBar > li#appTitle {
  background-color: darkblue;
  color: black;
  font-weight: bold;
  list-style-type: none;
  float: left;
  display: block;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

ul.NavBar {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: lightblue;
}

ul.NavBar > a > li {
  float: left;
  display: block;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

ul.NavBar > a > li:hover{
  background-color: blueviolet;
  color: white
}
a.courseCard {
  box-shadow: 0 5px 10px 0 rgba(0,0,0,0.2);
  float: left;
  padding: 0 2%;
  margin: 1%;
  background-color: lightblue;
  text-align: center;
  text-decoration: none;
  color: black;
  width: 100px;
}

a.card:hover  {
  font-weight: bold;
  box-shadow: 0 10px 20px 0 rgba(0,0,0,0.2);
}
